import router from '@/router';
import Vue from 'vue'
import Vuex from 'vuex'
import createId from '../lib/createId';
Vue.use(Vuex)

const store= new Vuex.Store({
    state: {
      recordList:[],
      tagList:[],
      tagListDefault:[
        {tagId:'1',tagName:'',iconName:'game',type:''},
        {tagId:'2',tagName:'',iconName:'food',type:''},
        {tagId:'3',tagName:'',iconName:'education',type:''},
        {tagId:'4',tagName:'',iconName:'shopping',type:''},
        {tagId:'5',tagName:'',iconName:'traffic',type:''},
        {tagId:'6',tagName:'',iconName:'gift',type:''},
        {tagId:'7',tagName:'',iconName:'beer',type:''},
        {tagId:'8',tagName:'',iconName:'borrow',type:''},
        {tagId:'9',tagName:'',iconName:'prize',type:''},
        {tagId:'10',tagName:'',iconName:'friend',type:''},
        {tagId:'11',tagName:'',iconName:'second',type:''},
        {tagId:'12',tagName:'',iconName:'salary',type:''},
        
      ],
      iconBase:[{iconName:'food',type:''},{iconName:'education',type:''},
      {iconName:'game',type:''},{iconName:'management',type:''},
      {iconName:'prize',type:''},{iconName:'salary',type:''},
      {iconName:'second',type:''},{iconName:'shopping',type:''},
      {iconName:'tie',type:''},{iconName:'traffic',type:''},
      {iconName:'gift',type:''},{iconName:'borrow',type:''},
      {iconName:'part-time',type:''},{iconName:'health',type:''},
      {iconName:'dog',type:''},{iconName:'stock',type:''},
      {iconName:'barbecue',type:''},{iconName:'gather',type:''},
      {iconName:'hotel',type:''},{iconName:'movie',type:''},
      {iconName:'baseball',type:''},{iconName:'chat',type:''},
      {iconName:'friend',type:''},{iconName:'beer',type:''}]
    } as RootState,
    mutations: {
      updateTag(state,[tagId,tagName,iconName,type]){
        let tagNames= state.tagList.filter(item=>item.tagName===tagName)
        if(tagId==='0'){
          if(tagNames.length>0){
            alert('')
          }else{
            tagId = createId().toString()
            const newTag = {tagId:tagId,tagName:tagName,iconName:iconName,type:type} 
            state.tagList.push(newTag)
            store.commit('saveTagList')
            router.back()
          }
      }else{
        if(tagNames.length>1){
          alert('')
        }else{
          let tagIds =state.tagList.map(item=>item.tagId);
          let index = tagIds.indexOf(tagId);
          state.tagList[index].tagName=tagName
          state.tagList[index].iconName=iconName
          store.commit('saveTagList')
          router.back()
        }
      }
      },
      fetchRecordList(state){
        state.recordList=JSON.parse(localStorage.getItem('recordList')||'[]')
    },
      saveRecord(state,record:RecordItem){
        record.date=new Date().toISOString()
        let record2 = JSON.parse(JSON.stringify(record))as RecordItem
        state.recordList.push(record2)
        localStorage.setItem('recordList',JSON.stringify(state.recordList))
    },
    fetchTagList(state){
      if(localStorage.getItem('tagList')){
        state.tagList=JSON.parse(localStorage.getItem('tagList')!)
      }else{
        state.tagList=state.tagListDefault  
      }
    },
    saveTagList(state){
      localStorage.setItem('tagList',JSON.stringify(state.tagList))
    },
    removeTag(state,tagId){
      let tagIds =state.tagList.map(item=>item.tagId)
      let index = tagIds.indexOf(tagId)
      state.tagList.splice(index,1)
      store.commit('saveTagList') 
    },
  },
  actions: {
  },
  modules: {
  }
})

export default store
